body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.topbar{
    background-color: rgba(192, 206, 300, .3);
    height:20vh;
    width:auto;
    font-weight: lighter;
    
    border-bottom: 3px;
    border-bottom-style: groove;
    border-bottom-color:#325d80;

}

.title{
    position: relative;
    float: left;
    text-align: left;
    padding-left: 10vw;
    padding-top: 3vh;
    font-size: 8vh;
    
}
.information{
    position: absolute;
    top:9vh;
    right: 5vh;
  
    height:auto;
    float: right;
    text-align:left;
 
    padding-right: 10vw;

    font-size: 2vh;
}
.information-text {

}

.title-directory {
    text-align: left;
    position: absolute;
    padding-left: 10vw;
    padding-top: 15vh;
    float: left;
    font-size: 2vh;
}

a{text-decoration: none;}

a:hover{
    font-style: italic;
    color: #ffffff !important;
    
}
a:active {
    font-style: italic;
    color: #82a0b4 !important;
}
a:visited{
    text-decoration: none;
    color: #000000;
}



.centerbox {
    display: block;
    overflow: hidden;
    -webkit-scroll-snap-type: y proximity;
        -ms-scroll-snap-type: y proximity;
            scroll-snap-type: y proximity;
    margin: auto;
    margin-top: 10vh;
    width:50vw;
    height:50vh;
    overflow-y: scroll;
    font-size: 5vw;
    text-align: center;
    font-family: 'Mono45 Headline';
    color: #ffffff;
    scroll-behavior: smooth;
    background-color:rgba(192, 206, 300, .5);

    
}
::-webkit-scrollbar {
    display: none;
    }




button.image{
    padding: 0px;
    display:inline-block;
    font-size: 5vw;
    text-align: center;
    font-family: 'Mono45 Headline';
    color: #ffffff;
    opacity: .8;
    scroll-snap-align: center;
    vertical-align: middle;
    line-height: 50vh;
    overflow-y: scroll;
    height:50vh;
    width:50vw;
    background-image: url(/personalwebsite/static/media/image.032f2c80.png);
    background-size: 100% 100%;
    text-shadow: -2px 0 black, 0 2px black;
    cursor: pointer;
    transition: opacity .5s ease-in-out;
}

button.image:hover, button.image2:hover,button.image3:hover,button.image4:hover{
    opacity:1;
    transition: opacity .5s ease-in;
}

button.image2{
    background-image: url(/personalwebsite/static/media/image2.00513f92.png);
}
button.image3{
    background-image: url(/personalwebsite/static/media/image3.b2df0e28.png);
}
button.image4{
    background-image: url(/personalwebsite/static/media/image4.aff6999c.png);
}

button{
    border:none;
}
button:focus{
    outline: 0px;
}
.image-content {
    display: inline-block;
    overflow: hidden;
    scroll-snap-align: start;
    margin-left: auto;
    margin-right: auto;
    padding: 5vw 5vh;
    color: black;
    font-size: 20px;
    text-align: center;
    vertical-align: middle;
    max-height: auto;
    width: auto;
    
    
    opacity: 1;
    background-color: rgba(192, 206, 300, .3);
    
    transition: max-height 0.5s ease-in-out;
  }

img{
    scroll-snap-align:start;
    margin-left: auto;
    margin-right: auto;
    margin-top:3vh;
    border-style: solid;
    border-color: #325d80;
    display: inline-block;
    width: auto;
}
